// App entry point
define('glympse-viewer-client-adapter/adapter/ViewClientAdapterDefines',['require','exports','module'],function(require, exports, module)
{
    'use strict';
	
	var Defines =
	{
		  STATE: {
			  Arrived: 'Arrived'
			, Avatar: 'Avatar'
			, Eta: 'Eta'
			, Expired: 'Expired'
			, Name: 'Name'
			, NoInvites: 'NoInvites'
			, Phase: 'Phase'
		}
	
		, REQUESTS: {
			  addInvites: 'addInvites'
			, addGroups: 'addGroups'
			, addMarkers: 'addMarkers'
			, addTwitterTopics: 'addTwitterTopics'
			, addTwitterUsers: 'addTwitterUsers'
			, generateRoute: 'generateRoute'
			, ignoreDestinations: 'ignoreDestinations'
			, refreshView: 'refreshView'
			, removeInvites: 'removeInvites'
			, setApiServices: 'setApiServices'
			, setPadding: 'setPadding'
			, setUserInfo: 'setUserInfo'	// Send-only... break it out?
			, updateSetting: 'updateSetting'
		}
		
		// APIs that are only available for client adapter users due to
		// non-serializable content that can't be passed across frame boundaries
		, REQUEST_LOCAL: {
			  getInvites: 'getInvites'
			, getMap: 'getMap'
		}

		, MSG: {
			  DataUpdate: 'DataUpdate'
			, StateUpdate: 'StateUpdate'
			, ViewerInit: 'ViewerInit'
			, ViewerReady: 'ViewerReady'
		}
	};
	
		
	// Global namespace registration
	if (!window.glympse)
	{
		window.glympse = {};
	}
	
	if (!window.glympse.ViewClientAdapterDefines)
	{
		window.glympse.ViewClientAdapterDefines = Defines;
	}

	
	module.exports = Defines;
});

